import { Component } from '@angular/core';
import { AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-parqueocobro-list',
  templateUrl: 'parqueocobro-list.html',
})
export class ParqueocobroListPage {

  public loading: boolean = true;
  items: any=[];  
  items2: any=[];

  sidist:boolean=false;
  public hasFilter: boolean = false;
  tiposus: any=[];
 
  idsuscriptor: any;
  username: string = "";
  nidsuscriptor: any;
  idempresa: any;
  idafiliado:any='na';

  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';

  constructor( public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  





      
  showFilter3()     
  {


    let prompt = this.alertCtrl.create({
      title:'Cobro, Ingrese CUI',
      inputs: [
        {   
          name: 'CUI',
          placeholder: 'Ingrese CUI'
          
        }
      ],
      buttons: [
        {
          text: 'Cancelar'
        },
        {
          text: 'Buscar',
          handler: todo => {
 
                if(todo){
 
             
                  this.regService.getReg( todo.CUI+'/CUI','Entradasdpis').then((data) => {
                    this.hasFilter = true;
                    this.items=data;
                   
                }, (err) => {
                  this.msj.showError3('Error',err.error);
                });

                
                  
 
                }
 
 
          }
        }
      ]
    });
 
    prompt.present();

  }

  pideregistros() { 
      let self = this;
      self.loading = true;

      
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
  this.idsuscriptor=this.tiposus[3]

  if(this.tiposus[0]=='Distribuidor')
  {
    this.sidist=true;
  }
  else
  {
    this.sidist=false;
  }

  this.sidist=true;
  this.regService.getReg( 'todos/todos','Entradasdpis').then((data) => {
 
    this.items=data;
    self.loading = false;
}, (err) => {
  this.msj.showError3('Error',err.error);
});
});


this.sidist=true; 
  }
  ionViewDidLoad() {
    


  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 

  

  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  
  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'Entradasdpis').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
        
      
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });  
  alert.present();

  
    
  }
  value:any;
  
  updateRecord2(item : any,no) : void
  {
    var link=this.regService.daserverenuso0()+ "/api/entradasdpis/"+ item._id+"/PDF"
  

    this.regService.abrelinkinterno3(link,'_blank','',0);

 


  }


  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('ParqueocobroCreatePage', { record : item,idempresa:this.navParams.data.idempresa },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('ParqueocobroCreatePage',{ record : null,idempresa:this.navParams.data.idempresa },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
  // this.items.concat([{idcomprasaldo:data.idcomprasaldo,idsuscriptor:data.idsuscriptor,fecha:data.fecha,monto:data.monto,nombre:data.noplaca}]);
      }  
    })
    addModal.present();
      //this.navCtrl.push('EmpresaCreatePage');
   }
   viewRecord(item : any): void
   {
     // let modal = this._MODAL.create(ViewGalleryPage, { record : item });
     // modal.present();
   }

   excludeTracks: any = [];

   presentFilter() {
    
  }

  



}



